ul {
  list-style: none;
  padding-inline-start: 0px;
  margin-block-start: 0px;
  margin-block-end: 0px;
}

.MuiList-root {
  display: flex;
  flex-direction: column;
}
.build-list-container {
    display: flex;
    flex-direction: column;
    position: absolute;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 100%;
    background-color: white;
    z-index: 999;
}
.build-list {
  width: 70vw;
}
.build-list-table, .build-list-table tbody, .build-list-table thead, .build-list-table tr {
  width: 100%;
  text-align: left;
  vertical-align: top;
  border-collapse: collapse;
  border-spacing: 0;
  color: #333;
}
.build-list-section-header {
  text-transform: uppercase;
  background: #43b3d2; 
}
.build-list-row-header {
  text-transform: capitalize;
}
.build-list-table th, .build-list-table td {
  padding: 5px;
  border-top: 1px solid gray;
}
.build-list-table tbody tr:nth-child(even) th, .build-list-table tbody tr:nth-child(even) td {
  background-color: #f0f0f0;
}
#peep-show-container {
  display: flex; 
  flex-direction: column; 
  align-items: center;
  height: 100vh;
}
#image-app-nav {
  width: 100% !important;
  position: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5em;
  z-index: 1000;
}
#image-uploader-nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 50px;
}
#bike-selection-dropdown {
  top: -8px;
}
a {
  color: inherit;
  text-decoration: inherit;
}

.nav-link {
  align-self: center;
  justify-self: center;
  margin: 0px 20px;
}
.nav-link a {
  font-weight: bold;
  font-family: Righteous, sans-serif;
  text-transform: uppercase;
  font-size: 19px;
}
.page-content {
  padding-top: 0px;
}

.loading-spinner {
  margin-top: 5em;
  min-height: 500px;
}

#first-placeholder-message {
  margin-top: 90px;
}

@media only screen and (min-width: 769px) {
  #image-app-nav {
    top: 64px;
  }
}

@media only screen and (max-width: 768px) {
  #first-placeholder-message {
    margin-top: 120px;
  }

  #image-app-nav {
    top: 58px;
  }
}
.bike-card-img {
    width:  300px;
    height: 225px;
    object-fit: cover;
}
.bike-card-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 17px;
  font-weight: bold;
  padding: 5px;
  background: -webkit-gradient(linear,left top,left bottom,from(0),color-stop(0,rgba(0,0,0,.6)),color-stop(50%,rgba(0,0,0,.3)),to(transparent));
  background: linear-gradient(0,rgba(0,0,0,.6),rgba(0,0,0,.3) 50%,transparent);
  width: 100%;
}
.bike-card-footer h6 {
  color: white;
  font-size: 17px;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 17px;
}
#root {
  height: max-content;
  overflow-x: overlay;
}
#root >div {
  height: 100% !important;
}
.MuiPaper-rounded {
  border-radius: 0 !important;
}
.bike-selection-dropdown-link {
  display: block;
  width: 100%;
  padding: 2px 5px;
}